// useScrollToLatestPrompt.js
// This custom hook provides a function to smoothly scroll to the latest prompt element.

import { easeInOutCubic } from '../utils/textUtils'; // Assuming this utility function is defined

/**
 * A custom React hook to provide a function for smooth scrolling to the latest prompt element.
 * @returns {function(number): void} A function that, when called, scrolls to the latest prompt.
 */
const useScrollToLatestPrompt = () => {
  /**
   * Smoothly scrolls the conversation container to the latest prompt element.
   * @param {number} scrollDuration The duration of the scroll animation in milliseconds. Defaults to 1000ms.
   */
  const scrollToLatestPrompt = (scrollDuration = 1000) => {
    // Select the main conversation container element
    const conversationContainer = document.querySelector('.conversation-container_Main');
    if (!conversationContainer) {
      console.warn("Conversation container not found. Cannot scroll.");
      return;
    }

    // Select all elements identified as prompt elements within the container
    // This is the key selector: '.prompt-overflow-auto'
    const promptElements = conversationContainer.querySelectorAll('.prompt-overflow-auto');

    // Proceed only if there are prompt elements found
    if (promptElements.length > 0) {
      // Get the very last (latest) prompt element, which should be the "Result Summary"
      const latestPrompt = promptElements[promptElements.length - 1];

      // Define a small offset to scroll a bit further down past the element.
      // This creates some breathing room below the latest message.
      const scrollOffset = 70; // Value in pixels, adjusted for better spacing

      // Calculate the starting scroll position of the container
      const startScrollPosition = conversationContainer.scrollTop;

      // Calculate the target scroll position:
      // It's the top of the latest prompt, minus the container's offset from its parent
      // plus the height of the latest prompt, plus the defined scroll offset.
      // This will scroll to the bottom of the 'latestPrompt' element + the offset.
      const targetScrollPosition = (latestPrompt.offsetTop - conversationContainer.offsetTop) + latestPrompt.clientHeight + scrollOffset;

      // Record the start time for the animation
      const startTime = performance.now();

      /**
       * The animation frame function for smooth scrolling.
       * @param {DOMHighResTimeStamp} currentTime The current time provided by requestAnimationFrame.
       */
      const animateScroll = (currentTime) => {
        const timeElapsed = currentTime - startTime;
        // Calculate progress as a value from 0 to 1
        const progress = Math.min(timeElapsed / scrollDuration, 1);
        // Apply an easing function for a smoother animation (e.g., easeInOutCubic)
        const easedProgress = easeInOutCubic(progress);

        // Calculate the current scroll position based on eased progress
        conversationContainer.scrollTop = startScrollPosition + (targetScrollPosition - startScrollPosition) * easedProgress;

        // Continue the animation if not yet finished
        if (timeElapsed < scrollDuration) {
          requestAnimationFrame(animateScroll);
        }
      };

      // Start the animation
      requestAnimationFrame(animateScroll);
    }
  };
  return scrollToLatestPrompt;
};

export default useScrollToLatestPrompt;
