import React from 'react';
import { Card } from 'primereact/card';
import { InputTextarea } from 'primereact/inputtextarea';
const PromptInputForm = ({ prompt, setPrompt, handleSubmit, loading }) => {
  const handleKeyPress = (event) => {
    //console.log("Event received:", event); // Add this
    if (event) { // Keep this check for robust debugging
      if (event.key === 'Enter' && !event.shiftKey) {
        event.preventDefault();
          if (prompt && prompt.trim() !== '' && !loading) {
            handleSubmit();
          }
      }
    } else {
      //console.error("Event object is undefined!", event); // More specific error
    }
  };
  return (
    <Card className="prompt-div footer">
      <InputTextarea
        value={prompt}
        onChange={(e) => setPrompt(e.target.value)}
        onKeyPress={handleKeyPress}
        rows={2}
        cols={30}
        placeholder="Enter your prompt here..."
        autoResize // Add autoResize for better UX, if desired
      />
      {prompt && prompt.trim() !== '' && (
        <button
          onClick={handleSubmit}
          disabled={loading}
          className="send-message-button"
          aria-label="Send message"
        >
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" style={{ width: '24px', height: '24px' }}>
            <path d="M2.01 21L23 12 2.01 3 2 10l15 2-15 2z"/>
          </svg>
        </button>
      )}
    </Card>
  );
};
export default PromptInputForm;
