import React from 'react';
import { marked } from 'marked';
import DOMPurify from 'dompurify';
import { Card } from 'primereact/card';
import { linkifyText } from '../utils/textUtils';

//renderBoldMarkdown
const renderBoldMarkdown = (text) => {
   const html = DOMPurify.sanitize(marked.parse(text));
   return html;
};
// ParagraphRenderer
const ParagraphRenderer = (text) => {
  let processedText = linkifyText(text);
  const html = DOMPurify.sanitize(marked.parse(processedText), {
        ADD_ATTR: ['target']
      });
  return html;
};
//ConversationDisplay
const ConversationDisplay = ({ conversation, isSourceOpenMap, setIsSourceOpenMap }) => {
  const NO_ANSWER_REGEX = new RegExp(process.env.REACT_APP_NO_ANSWER_PATTERNS, 'i');
  return (
    <>
      {conversation.length > 0 ? (
        conversation.map((item, i) => (
          <Card
            key={i}
            className={`w-70 mb-2 ${item.prompt ? 'float-right prompt-overflow-auto' : 'float-left'}`}
          >
            {item.prompt && <p>{item.content}</p>}

            {!item.prompt && item.error && <p className="text-red-500">{item.content}</p>}

            {!item.prompt && !item.error && item.content?.predictions && (
              <div className="results-container">
                <div className="results-box">
                  <div className="results-header">
                    <h3 className="results-title">Result Summary:</h3>
                    <p className="results-text">
                      {item.content.predictions[0]?.result || 'No result available'}
                    </p>
                  </div>

                  {/* Conditional rendering for Source Documents */}
                  {item.content.predictions[0]?.result &&
                   !NO_ANSWER_REGEX.test(item.content.predictions[0].result) && // This is the corrected line
                   item.content.predictions[0]?.source_documents?.length > 0 && (
                    <div className="source-documents">
                      <div
                        className="source-header cursor-pointer py-2 flex justify-between items-center"
                        onClick={() => setIsSourceOpenMap((prev) => ({ ...prev, [i]: !prev[i] }))}
                      >
                        <h4 className="source-title">Source Documents:</h4>
                        <span className="toggle-icon">{isSourceOpenMap[i] ? '▲' : '▼'}</span>
                      </div>

                      <div className={`source-list-container ${isSourceOpenMap[i] ? 'open' : ''}`}>
                        <ul className="source-list pl-4">
                          {item.content.predictions[0].source_documents.map((doc, index) => (
                            <li key={index} className="source-item mb-2">
                              <p>
                                <strong>{doc?.type || 'N/A'} {index + 1}:</strong>
                              </p>
                              <div className="labelSetting">
                                <strong>ID:</strong><span dangerouslySetInnerHTML={{ __html: renderBoldMarkdown(doc.metadata?.id || 'N/A') }} />

                              </div>
                              <div className="labelSetting">
                                  <strong>Label:</strong> <span dangerouslySetInnerHTML={{ __html: renderBoldMarkdown(doc?.metadata?.label || 'N/A') }} />

                              </div>
                              <p>
                                <strong>Page Content:</strong>
                              </p>
                              <div><span dangerouslySetInnerHTML={{ __html: ParagraphRenderer(doc.page_content) }} /></div>
                              {doc.metadata?.result_summary && (
                                <div className="source-summary">
                                  <h6 className="source-summary-title">
                                    Result Summary for this Source:
                                  </h6>
                                  <p className="source-summary-text"><span dangerouslySetInnerHTML={{ __html: renderBoldMarkdown(doc.metadata.result_summary) }} /></p>
                                </div>
                              )}
                            </li>
                          ))}
                        </ul>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            )}
          </Card>
        ))
      ) : (
        <div className="text-center WelcomeText text-gray-500 mt-8">
          <p>Health Insurance Reserves Model Regulation Query Tool.</p>
        </div>
      )}
    </>
  );
};
export default ConversationDisplay;
