export const linkifyText = (text) => {
  const newLink = process.env.REACT_APP_DOCUMENT_LINK
  const fileExtensions = ['pdf', 'doc', 'docx', 'xls', 'xlsx', 'csv', 'txt'];
  const fileExtPattern = `(?:${fileExtensions.join('|')})`;
  // Original file-specific patterns (from your existing code)
  const fileRegexPart1 = `(https?:\\/\\/[\\w\\d.:%#\$&'()*+,;=?@\\[\\]~\\/\\s-]+\\.(?:${fileExtPattern})\\b|www\\.[\\w\\d.:%#\$&'()*+,;=?@\\[\\]~\\/\\s-]+\\.(?:${fileExtPattern})\\b)`;
  const fileRegexPart2 = `(\\b[\\w\\-.]+\\.(?:${fileExtPattern})\\b)`;
  const fileRegexPart3 = `((?!https?:\\/\\/|www\\.)[\\w\\d.-]+\\.[a-z]{2,}(?:\\/[\\w\\d.:%#\$&'()*+,;=?@\\[\\]~\\/\\s-]+\\.(?:${fileExtPattern}))?)`;

  // General URL patterns
  const generalUrlRegexPattern = `(https?:\\/\\/[^\\s]+)|(www\\.[^\\s]+)|(\\b[a-zA-Z0-9-]+\\.[a-zA-Z]{2,}(?:\\/[^\\s]*)?\\b)`;

  const combinedRegex = new RegExp(
    `${fileRegexPart1}|${fileRegexPart2}|${fileRegexPart3}|${generalUrlRegexPattern}`,
    'gi'
  );
  // Pre-compile regexes for testing inside map function to avoid re-creation on each iteration
  const isFileUrlPatternTest = new RegExp(`\\.${fileExtPattern}\\b`, 'i');
  const isNakedFileLinkPatternTest = new RegExp(`^\\b[\\w\\-.]+\\.(?:${fileExtPattern})\\b$`, 'i');
  const isPureGeneralUrlPatternTest = new RegExp(`^(https?:\\/\\/[^\\s]+|www\\.[^\\s]+|\\b[a-zA-Z0-9-]+\\.[a-zA-Z]{2,}(?:\\/[^\\s]*)?\\b)$`, 'i');
  const parts = text.split(combinedRegex);
  let result = [];
  parts.forEach((part) => {
    // Handle empty, null, or undefined parts from split
    if (part === undefined || part === null || part === '') {
      return; // Skip these parts
    }

    const createLinkHtml = (href, content, className) => {
      // Use string concatenation or template literals to build the HTML string
      return `<a  target="_blank" class="documentHrefLink" href="${href}" rel="noopener noreferrer" class="${className}">${content}</a>`;
    };
    const trimmedPart = part.trim();
    const isFileUrl = isFileUrlPatternTest.test(trimmedPart);

    if (isPureGeneralUrlPatternTest.test(trimmedPart) && !isFileUrl) {
      let href = trimmedPart;
      if (!href.startsWith('http://') && !href.startsWith('https://')) {
        href = `http://${href}`;
      }
      result.push(createLinkHtml(encodeURI(href), part, "text-blue-600 underline hover:text-blue-800"));
    } else if (
      (trimmedPart.startsWith('http://') || trimmedPart.startsWith('https://') || trimmedPart.startsWith('www.')) &&
      isFileUrl
    ) {
      const encodedHref = trimmedPart.startsWith('http') ? encodeURI(trimmedPart) : encodeURI(`http://${trimmedPart}`);
      result.push(createLinkHtml(encodedHref, part, "text-blue-600 underline hover:text-blue-800"));
    } else if (isNakedFileLinkPatternTest.test(trimmedPart)) {
      result.push(createLinkHtml(`${newLink}${encodeURIComponent(trimmedPart)}`, part, "text-blue-600 underline hover:text-blue-800"));
    } else if (
      !trimmedPart.startsWith('http') &&
      !trimmedPart.startsWith('www.') &&
      isFileUrl
    ) {
      result.push(createLinkHtml(`http://${encodeURI(trimmedPart)}`, part, "text-blue-600 underline hover:text-blue-800"));
    } else {
      // If none of the above conditions match, add the part as plain text
      result.push(part);
    }
  });
  // Join all parts back into a single HTML string
  return result.join('');
};
//easeInOutCubic
export const easeInOutCubic = (t) => {
  return t < 0.5 ? 4 * t * t * t : 1 - Math.pow(-2 * t + 2, 3) / 2;
};
