import React, { useState } from 'react';
import ConversationDisplay from '../components/ConversationDisplay';
import LoadingIndicator from '../components/LoadingIndicator';
import PromptInputForm from '../components/PromptInputForm';
import useScrollToLatestPrompt from '../hooks/useScrollToLatestPrompt';
import chatBotService from '../api/chatbotapi';
import ressponse_js from '../response_js.json';
import './PromptForm.css';
const PromptForm = () => {
  const [conversation, setConversation] = useState([]);
  const [prompt, setPrompt] = useState(''); //default
  const [loading, setLoading] = useState(false);
  const [isSourceOpenMap, setIsSourceOpenMap] = useState({});
  const scrollToLatestPrompt = useScrollToLatestPrompt();
  const handleSubmit = async (e) => {
    //e.preventDefault();
    setLoading(true);
    setPrompt('');
    setConversation((prev) => [...prev, { content: prompt, prompt: true }]);
    setTimeout(scrollToLatestPrompt, 100);
    try {

      //
      //const res = ressponse_js;
      const res = await chatBotService.postQueryData(prompt);
      //console.log(res);
      setConversation((prev) => [...prev, { content: res }]);
      //   setConversation((prev) => {
      //     console.log(prev);
      //     return [...prev, { content: res }]
      //
      // });
    } catch (error) {
      if (error.response && error.response.status === 404) {
        setConversation((prev) => [
          ...prev,
          { error: true, content: 'No data found.' },
        ]);
      } else {
        setConversation((prev) => [
          ...prev,
          { error: true, content: 'Sorry, something went wrong.' },
        ]);
      }
    } finally {
      setLoading(false);
    }
  };
  return (
    <>
      <section className="conversation-container_Main">
        <ConversationDisplay
          conversation={conversation}
          isSourceOpenMap={isSourceOpenMap}
          setIsSourceOpenMap={setIsSourceOpenMap}
        />
        {loading && <LoadingIndicator />}
      </section>
      <PromptInputForm
        prompt={prompt}
        setPrompt={setPrompt}
        handleSubmit={handleSubmit}
        loading={loading}
      />
    </>
  );
};

export default PromptForm;
